import * as THREE from 'three';
import { RendererThree } from './Base';
import { PixelMap } from '../PMapTerrain';

export class Renderer extends RendererThree {
    public canvas: HTMLCanvasElement;
    public pointLight: THREE.PointLight;

    rayCaster = new THREE.Raycaster();

    mouse2 = new THREE.Vector2();
    mouse3: THREE.Vector3 | undefined = undefined;
    sphere: THREE.Mesh;
    surfaceMesh: THREE.Mesh;

    preSelection: THREE.Mesh | undefined = undefined

    public meshList: THREE.Mesh[] = [];
    public arrowList: THREE.ArrowHelper[] = [];

    constructor(id: string = 'main') {
        super(id)

        this.pointLight = new THREE.PointLight(0xffffff, 0.75);
        this.pointLight.position.x = 80;
        this.pointLight.position.z = 80;
        this.scene.add(this.pointLight);

        this.canvas = document.createElement('canvas');
        this.hostDiv.append(this.canvas);

        this.renderer.setClearColor(0x000000, 0);
        this.scene.background = null;
        this.camera.position.set(0, -80, 80);

        const material = new THREE.MeshBasicMaterial({ color: 0x00ff00 });
        const geometrySphere = new THREE.SphereGeometry(0.05, 50, 50);
        const objectSphere = new THREE.Mesh(geometrySphere, material);
        objectSphere.translateX(1);
        this.sphere = objectSphere;
        this.scene.add(objectSphere);


        this.start();
    }
    public reset() {
        this.scene.remove(...this.meshList)
        this.meshList = []

        this.scene.remove(...this.arrowList)
        this.arrowList = []

        this.preSelection = undefined;
    }
    public buildPoint(pMap: PixelMap) {

        for (let i = 0; i < pMap.pixelList.length; ++i) {
            const vec = pMap.pixelList[i].vec;
            const col = pMap.pixelList[i].color;

            const geometry = new THREE.SphereGeometry(0.15, 8, 8);
            const material = new THREE.MeshBasicMaterial({ color: new THREE.Color(col[0] / 255, col[1] / 255, col[2] / 255) });

            const mesh = new THREE.Mesh(geometry, material);
            mesh.userData['pixel'] = pMap.pixelList[i]
            pMap.pixelList[i].mesh = mesh;

            this.meshList.push(mesh);
            mesh.position.set(vec.x, vec.y, vec.z);
            this.scene.add(mesh);

        }
    }
    public mouseClick(e: MouseEvent): void {
        const obj = picking(e, this.meshList, this.renderer, this.camera, this.rayCaster, this.mouse2)
        // console.log(obj)
        if (obj) {
            if (this.preSelection) {
                const mat = this.preSelection.material as THREE.MeshBasicMaterial
                if (mat.color) mat.color.setRGB(this.preSelection.userData.pixel.color[0] / 255, this.preSelection.userData.pixel.color[1] / 255, this.preSelection.userData.pixel.color[2] / 255)
            }

            this.mouse3 = new THREE.Vector3(obj.mouse3.x, obj.mouse3.y, obj.mouse3.z);

            this.sphere.position.set(this.mouse3.x, this.mouse3.y, this.mouse3.z);

            this.onClickBind(this.mouse3.x, this.mouse3.y, this.mouse3.z);
            const mesh = obj.mesh[0].object as THREE.Mesh;
            // console.log(mesh)
            const mat = mesh.material as THREE.MeshBasicMaterial
            if (mat.color) mat.color.setHex(0xff0000)
            this.preSelection = mesh
            return
        }

        this.mouse3 = undefined;
    }
    public onClickBind(x: number, y: number, z: number) { /* bind needed */ }

    public buildMeshByImg = (pMap: PixelMap) => {
        // ................................................................................
        // your code goes here
        const xNum = pMap.rx;
        const yNum = pMap.ry;

        const vertices = [];
        for (let y = 0; y < yNum; ++y) {
            for (let x = 0; x < xNum; ++x) {
                const pix = pMap.getPixel(x, y)
                vertices.push(pix.vec.x, pix.vec.y, pix.vec.z);
            }
        }

        const faces = [];
        for (let y = 0; y < yNum - 1; ++y) {
            for (let x = 0; x < xNum - 1; ++x) {
                let index = x + (y * xNum);
                faces.push(index, index + xNum, index + 1,);
                faces.push(index + 1, index + xNum, index + xNum + 1,);
            }
        }

        const material = new THREE.MeshNormalMaterial({
            // side: THREE.DoubleSide,
            vertexColors: true,
            flatShading: false,
        });
        const geometry = new THREE.BufferGeometry();
        geometry.setIndex(faces);
        geometry.setAttribute('position', new THREE.Float32BufferAttribute(vertices, 3));

        geometry.computeVertexNormals();
        geometry.computeBoundingBox();

        const mesh = new THREE.Mesh(geometry, material);
        this.surfaceMesh = mesh
        this.meshList.push(mesh);
        this.scene.add(mesh);

    }
    public buildArrow(pMap: PixelMap, size = 0.25 ) {
        const zOff = 0.0075;
        for (let j = 0; j < pMap.pixelList.length; ++j) {
            const px = pMap.pixelList[j];
            const origin = new THREE.Vector3(px.vec.x, px.vec.y, px.vec.z + zOff);
            px.arrow = [];

            for (let i = 0; i < px.nPix.length; ++i) {
                const np = px.nPix[i];
                const dir = new THREE.Vector3(np.vec.x - px.vec.x, np.vec.y - px.vec.y, np.vec.z + zOff - px.vec.z + zOff);
                dir.normalize();

                const slope = dir.z;
                const r = slope <= 0 ? Math.abs(slope): 0;
                const g = 0;
                const b = slope > 0 ? Math.abs(slope): 0;

                px.slope.push(`id:${np.id}; value:${slope}`);

                const arrowHelper = new THREE.ArrowHelper(dir, origin, size, new THREE.Color(r,g,b));
                px.arrow.push(arrowHelper);

                this.arrowList.push(arrowHelper);
                this.scene.add(arrowHelper);
            }

        }



    }
}


const picking = (e: MouseEvent, arrayMesh, renderer, camera, rayCaster, mouse2) => {
    mouse2.x = (e.offsetX / renderer.domElement.clientWidth) * 2 - 1;
    mouse2.y = -(e.offsetY / renderer.domElement.clientHeight) * 2 + 1;
    rayCaster.setFromCamera(mouse2, camera);
    const intersects = rayCaster.intersectObjects([...arrayMesh]);
    if (intersects.length > 0) {
        return {
            mesh: intersects,
            mouse3: new THREE.Vector3(intersects[0].point.x, intersects[0].point.y, intersects[0].point.z)
        };
    }
    return undefined;
}