import { AnyLayer } from 'mapbox-gl';
import * as THREE from 'three';

export class PixelMap {
    public pixelList: Pixel[] = []
    public minV: Vector3;
    public maxV: Vector3;
    public rx = 10;
    public ry = 10;
    public rz = 2;
    public intervalX;
    public intervalY;
    public selPixel: Pixel | undefined = undefined;
    constructor() {


    }
    public init(minV: Vector3, maxV: Vector3, rx: number, ry: number){
        this.minV = minV;
        this.maxV = maxV;
        this.rx = rx;
        this.ry = ry;

        this.pixelList = []
        const x = this.minV.x
        const y = this.minV.y

        this.intervalX = (this.maxV.x - this.minV.x) / (this.rx - 1)
        this.intervalY = (this.maxV.y - this.minV.y) / (this.ry - 1)
        
        for(let j = 0 ; j < this.ry; j++) {
            for(let i = 0 ; i < this.rx; i++) {
                const xV = x + i * this.intervalX;
                const yV = y + j * this.intervalY;

                let pix = new Pixel(this, xV * -1 , yV , 0); // flip x
                // let pix = new Pixel(this, xV * -1 , yV , 0);
                pix.id = this.pixelList.length;
                pix.xi = i;
                pix.yi = j;
                this.pixelList.push(pix);
            }
        }
        this.initGridNeighbor();
    }
    public getPixel(x: number, y: number){
        return this.pixelList[x + y * this.rx]
    }
    private initGridNeighbor() {
        for (let j = 0; j < this.ry; ++j) {
            for (let i = 0; i < this.rx; ++i) {
                let k = i + j * this.rx;
                let px = this.pixelList[k];
                if (i > 0) { px.nPix.push(this.pixelList[k - 1]); }
                if (i < this.rx - 1) { px.nPix.push(this.pixelList[k + 1]); }
                if (j > 0) { px.nPix.push(this.pixelList[k - this.rx]); }
                if (j < this.ry - 1) { px.nPix.push(this.pixelList[k + this.rx]); }
                if ((i > 0) && (j < this.ry - 1)) { px.nPix.push(this.pixelList[k + this.rx - 1]); }
                if ((i > 0) && (j > 0)) { px.nPix.push(this.pixelList[k - this.rx - 1]); }
                if ((i < this.rx - 1) && (j < this.ry - 1)) { px.nPix.push(this.pixelList[k + this.rx + 1]); }
                if ((i < this.rx - 1) && (j > 0)) { px.nPix.push(this.pixelList[k - this.rx + 1]); }
            }
        }
    }
    public updateImage(img: HTMLImageElement) {
        const canvas = document.createElement('canvas');
        const ctx = canvas.getContext('2d', {willReadFrequently: true})
        ctx.drawImage(img, 0, 0, img.width, img.height, 0, 0, this.rx, this.ry)

        const imgData = ctx.getImageData(0, 0, this.rx, this.ry);

        const px = (x: number, y: number) => {
            return x * 4 + y * imgData.width * 4;
        };

        for (let j = 0; j < this.ry; ++j) {
            for (let i = 0; i < this.rx; ++i) {
                let k = px(i, j)
                let indexList = i + j * this.rx;

                this.pixelList[indexList].color = [
                    imgData.data[k], imgData.data[k+1], imgData.data[k+2], imgData.data[k+3]
                ]
                this.pixelList[indexList].vec.z = RGBtoGray(
                    imgData.data[k] / 255, imgData.data[k+1] / 255, imgData.data[k+2] / 255
                ) * this.rz
            }
        }
    }
    public picking(x: number, y: number, z: number) {
        const mv = new Vector3(x,y,z)
        let dis = Number.MAX_VALUE;
        let id = -1
        for(let i = 0 ; i < this.pixelList.length; ++i) {
           const theDis = distance(this.pixelList[i].vec, mv);
           if (dis > theDis) {
            dis = theDis;
            id = i;
           }
           this.pixelList[i].hover = false;
        }

        this.selPixel = undefined;
        if (id > -1) {
            this.pixelList[id].hover = true;
            this.selPixel = this.pixelList[id];
            // console.log(this.selPixel)
        }
        return this.selPixel;
    }
    public render(ctx) {
        this.pixelList.forEach((p: Pixel) => {
            p.render(ctx);
        })

        if (this.selPixel) {
            this.selPixel.render(ctx);
        }
    }
    public onClick (x: number, y: number, z: number) {
        return this.picking(x, y, z)
    }
    public toCSV() {

        const csv: any[] = [
            ['id', 'location(lat, lon)', 'elevation', 'slope' ],
        ]

        for(let i = 0; i < this.pixelList.length; ++i) {
            const pix = this.pixelList[i];
            csv.push([
                pix.id, `${pix.vec.y},${pix.vec.x}`, `${pix.vec.z}`, `${pix.slope}`
            ]);
        }


        downloadBlob(arrayToCsv(csv), 'export.csv', 'text/csv;charset=utf-8;')

    }
}

class Pixel {
    public id = 0;
    public slope = [];
    public nPixString = [];
    
    public xi: number;
    public yi: number;
    public pMap: PixelMap;
    public vec: Vector3;
    public hover: boolean = false;
    public nPix: Pixel[] = [];
    public color: number[] = [];

    public mesh: any;
    public arrow: any = [];
    constructor(pMap: PixelMap, x: number, y: number, z: number) {
        this.pMap = pMap;
        this.vec = new Vector3(x,y,z);
    }
    public onClick() {
        // console.log(this)
    }
    public render(ctx: CanvasRenderingContext2D) {
        const width = this.pMap.intervalX ;
        const height = this.pMap.intervalY;

        ctx.beginPath();
        ctx.rect(this.vec.x - width * 0.5, this.vec.y - height * 0.5, width, height);
        ctx.closePath();
        ctx.stroke();
        if (this.hover) {
            ctx.fillStyle = '#555';
            ctx.fill();
        } else {
            ctx.fillStyle = `rgba(${this.color[0]}, ${this.color[1]}, ${this.color[2]}, ${this.color[3]})`;
            ctx.fill();
        }

        ctx.beginPath();
        ctx.arc(this.vec.x, this.vec.y, this.vec.r, 0, Math.PI * 2.0);
        ctx.closePath();
        if (this.hover) {
            ctx.fillStyle = '#999';
        } else {
            ctx.fillStyle = '#333';
        }
        ctx.fill();

        if (this.hover) {
            for(let i = 0 ; i < this.nPix.length; ++i) {
                ctx.beginPath();
                ctx.moveTo(this.vec.x, this.vec.y);
                ctx.lineTo(this.nPix[i].vec.x, this.nPix[i].vec.y);
                ctx.closePath();
                ctx.stroke();
            }

            ctx.strokeText(`x: ${this.xi}, y: ${this.yi}` ,this.vec.x, this.vec.y);
        }

        
    }
}

export class Vector3 {
    public x: number = 0.0;
    public y: number = 0.0;
    public z: number = 0.0;
    public r: number = 3
    constructor(x,y,z) {
        this.x = x;
        this.y = y;
        this.z = z;
    }
}


const distance = (v0: Vector3, v1: Vector3) => {
    return Math.sqrt(distance2(v0, v1));
}
const distance2 = (v0: Vector3, v1: Vector3) => {
    return (v0.x - v1.x) * (v0.x - v1.x) + (v0.y - v1.y) * (v0.y - v1.y) + (v0.z - v1.z) * (v0.z - v1.z);
}

export const RGBtoGray = (r: number, g: number, b: number) => {
    return 0.2126 * r + 0.7152 * g + 0.0722 * b;
}

function arrayToCsv(data){
    return data.map(row =>
      row
      .map(String)  // convert every value to String
      .map(v => v.replaceAll('"', '""'))  // escape double quotes
      .map(v => `"${v}"`)  // quote it
      .join(',')  // comma-separated
    ).join('\r\n');  // rows starting on new lines
  }

function downloadBlob(content, filename, contentType) {
    // Create a blob
    var blob = new Blob([content], { type: contentType });
    var url = URL.createObjectURL(blob);
  
    // Create a link to download it
    var pom = document.createElement('a');
    pom.href = url;
    pom.setAttribute('download', filename);
    pom.click();
  }